<template>
	<div class="login-wrapper">
	    <Form ref="formInline" :model="formInline" :rules="ruleInline">
	    	<FormItem>
	    		<h1 style="text-align: center;">前端权限控制</h1>
	    	</FormItem>
	        <FormItem prop="username">
	            <Input readonly type="text" size="large" v-model="formInline.username" placeholder="账号" style="width: 300px">
	                <Icon type="ios-person-outline" slot="prepend"></Icon>
	            </Input>
	        </FormItem>
	        <FormItem prop="password">
	            <Input readonly type="password" size="large" v-model="formInline.password" placeholder="密码" style="width: 300px">
	                <Icon type="ios-locked-outline" slot="prepend"></Icon>
	            </Input>
	        </FormItem>
	        <FormItem>
	            <Button type="primary" size="large" long @click="handleSubmit('formInline')">登录</Button>
	        </FormItem>
            <FormItem>
                <Button :type="`${formInline.username === 'admin' ? 'primary' : 'text'}`" size="small" @click="formInline.username = 'admin'">管理员登录</Button>
                <Button :type="`${formInline.username === 'user' ? 'primary' : 'text'}`" size="small" @click="formInline.username = 'user'">用户登录</Button>
            </FormItem>
	    </Form>
	</div>
</template>
<script>
	import AppModel from '@/model/app'
	import Bus from '../bus'
	const appInstance = new AppModel()
    export default {
        data () {
            return {
                formInline: {
                    username: 'admin',
                    password: 'sfsdfsdf'
                },
                ruleInline: {
                    user: [
                        { required: true, message: '请输入用户名', trigger: 'blur' }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        { type: 'string', min: 6, message: '密码长度最小6位数', trigger: 'blur' }
                    ]
                }
            }
        },
        created() {
        	// 清除sessionStorage中的token， 确保渲染当前页面， 意味着token要重新获取
        	appInstance.handleSession()
        },
        methods: {
            handleSubmit(name) {
                this.$refs[name].validate((valid) => {
                    if (valid) {
                       this.loginAjax() 
                    }
                })
            },
            async loginAjax(){
            	let res = null
            	if (this.formInline.username === 'admin') {
                	res = await appInstance.g_adminToken(this.formInline)
                } else if (this.formInline.username === 'user') {
                	res = await appInstance.g_userToken(this.formInline)
                } else {
                    console.log('重新登录')
                    return
                }
                if(res.data.token){
                	appInstance.handleSession('token', res.data)
                	Bus.$emit('on-after-login-success')
                }
            }
        }
    }
</script>

<style>
	.login-wrapper{
		position: fixed;
		top:0;
		left:0;
		right: 0;
		bottom: 0;
		display: flex;
		justify-content: center;
		align-items: center;
	}
</style>



